if ($(".leader-swiper").length) {
    var leaderswip = new Swiper(".leader-swiper", {
        slidesPerView: 1,
        speed: 1000,
        spaceBetween: 20,
        loop: false,
        centeredSlides: true,
        slideToClickedSlide: true,
        modules: [Autoplay, Navigation, Pagination],
        navigation: {
            nextEl: ".ri-next",
            prevEl: ".ri-prev",
        },
        autoplay: {
            delay: 3000,
            disableOnInteraction: false,
            pauseOnMouseEnter: true,
            mousewheel: true,
            
        },

        breakpoints: {
            0: {
                slidesPerView: 1,
                spaceBetween: 20,
            },
            480: {
                slidesPerView: 1,
                spaceBetween: 50,
                allowTouchMove: true,
            },
            769: {
                slidesPerView: 1,
                spaceBetween: 50,
                allowTouchMove: false,
            },
            1025: {
                slidesPerView: 1.5,
                spaceBetween: 50,
                allowTouchMove: false,
            },
            1366: {
                slidesPerView: 2,
                spaceBetween: 50,
                allowTouchMove: false,
            },
            1500: {
                slidesPerView: 3,
                spaceBetween: 20,
                allowTouchMove: false,
            },
        },
        on: {
            init: function () {
                this.autoplay.stop();
            },
            slideChange: function () {
                var textsField = $(
                    ".leader-info .container .wrapper .text-field .refence-detail"
                );
                var textsBox = $(
                    ".leader-info .container .wrapper .text-field .refence-detail .detail-box"
                );
                var textsBoxActive = textsBox.filter(".active");
                textsField.height(textsBoxActive.height());

                var selectFieldswip = $(".leader-info .swiper .swiper-slide");
                var selectID = this.activeIndex;
                textsBox.removeClass("active");
                selectFieldswip.removeClass("active");
                selectFieldswip.eq(selectID).addClass("active");

                $.each(textsBox, function () {
                    var boxID = $(this).data("id");
                    if (boxID == selectID) {
                        $(this).addClass("active");
                        textsField.height(textsBoxActive.height());
                    }
                });

                var activeIndex = this.activeIndex;
                var realIndex = this.slides[activeIndex].getAttribute(
                    "data-swiper-slide-index"
                );
                $(".swiper-slide").removeClass(
                    "swiper-slide-prev-prev swiper-slide-next-next"
                );
                $('.swiper-slide[data-swiper-slide-index="' + realIndex + '"]')
                    .prev()
                    .prev()
                    .addClass("swiper-slide-prev-prev");
                $('.swiper-slide[data-swiper-slide-index="' + realIndex + '"]')
                    .next()
                    .next()
                    .addClass("swiper-slide-next-next");
            },
        },
    });

    var textsFieldxx = $(
        ".leader-info .container .wrapper .text-field .refence-detail"
    );
  // textsFieldxx hover leaderswip autoplay stop
    textsFieldxx.on("mouseenter", function () {
        leaderswip.autoplay.stop();
    });
    // textsFieldxx hover leaderswip autoplay start
    textsFieldxx.on("mouseleave", function () {
        leaderswip.autoplay.start();
    });
    // leaderswip mouseenter autoplay stop
    leaderswip.el.onmouseover = function () {
        leaderswip.autoplay.stop();
    };
    // leaderswip mouseleave autoplay start
    leaderswip.el.onmouseout = function () {
        leaderswip.autoplay.start();
    };
  


    gsap.to(".leader-swiper", {
        scrollTrigger: {
            trigger: ".leader-swiper",
            start: "top 60%",
            end: "bottom 60%",
        },
        onComplete: () => {
            leaderswip.autoplay.start();
        },
    });
}
