if ($(".club-content-swiper").length) {
    var clubThumbs = new Swiper(".club-thumbs", {
        spaceBetween: 10,
        speed: 1000,
        modules: [Navigation, Pagination, Controller, Thumbs],
        lazy: true,
        watchSlidesProgress: true,
        loop: true,
        loopedSlides: 3,
        centeredSlides: true,
        slideToClickedSlide: true,
     
        breakpoints: {
            0: {
                slidesPerView: 1,
            },
            768: {
                slidesPerView: 1,
            },
            769: {
                slidesPerView: 3,
            },
            1200: {
                slidesPerView: 5,
            },
        },
    });
    var clubTop = new Swiper(".club-top", {
        spaceBetween: 0,
        speed: 1000,
        slidesPerView: 1,
        centeredSlides: true,
        loop: true,
        loopedSlides: 3,
        modules: [Navigation, Pagination, Controller, Thumbs],
        navigation: {
            nextEl: ".club-button-next",
            prevEl: ".club-button-prev",
        },

        on: {
            slideChange: function () {
                // nceki slaytta video varsa kaldrn
                setTimeout(() => {
                    $(".club-top .swiper-slide video").remove();
                }, 1000);
                $(".video-button").removeClass("active");
                $(".video-button .toggleDot").removeClass("active");

                // video-button pointer-events none ekle ve 100ms sonra kaldr
                $(".video-button").css("pointer-events", "none");
                setTimeout(() => {
                    $(".video-button").css("pointer-events", "auto");
                }, 1000);
            },
        },
    });

    clubTop.controller.control = clubThumbs;
    clubThumbs.controller.control = clubTop;
}

