let mobileSubmenuTrigger = document.querySelectorAll(".sub-trigger");

mobileSubmenuTrigger.forEach((trigger) => {
    trigger.addEventListener(
        "click",
        () => {
            // Dier alt menleri kapat
            mobileSubmenuTrigger.forEach((otherTrigger) => {
                if (otherTrigger !== trigger) {
                    otherTrigger.classList.remove("active");
                    otherTrigger.nextElementSibling.classList.add("lg:hidden");
                }
            });

            // Tklanan alt mennn durumunu deitir
            trigger.classList.toggle("active");
            trigger.nextElementSibling.classList.toggle("lg:hidden");
        },
        false
    );
});
