if ($(".counter-effect").length) {
    document.addEventListener("DOMContentLoaded", () => {
        gsap.utils.toArray(".counter-effect").forEach((element) => {
            setTimeout(() => {
                let value = element.querySelector(".value");
                let valueText = value.textContent;
                let numbers = valueText.split("");
                let valueHeight = element.querySelector(".value").offsetHeight;

                let numberElement =
                    '<div class="digit" style="height: ' +
                    valueHeight +
                    'px;"></div>';
                for (let i = 0; i <= 9; i++) {
                    numberElement +=
                        '<div class="digit w-fit leading-none" style="height: ' +
                        valueHeight +
                        'px;">' +
                        i +
                        "</div>";
                }

                value.innerHTML = "";
                value.style.height = valueHeight + "px";
                numbers.forEach((number) => {
                    value.innerHTML +=
                        '<div data-number="' +
                        (parseInt(number) + 1) +
                        '" class="number inline-flex flex-col [will-change:transform,width] backface-hidden">' +
                        numberElement +
                        "</div>";
                });

                value.querySelectorAll(".number").forEach((number, index) => {
                    number.querySelectorAll(".digit").forEach((digit) => {
                        if (digit.textContent == number.dataset.number - 1) {
                            digit.classList.add("active");
                        }
                    });

                    gsap.to(number, {
                        scrollTrigger: {
                            trigger: number.closest(".counter-effect"),
                            start: "top-=" + window.innerHeight / 2 + " center",
                        },
                        duration: 2,
                        y: -number.dataset.number * valueHeight,
                        width: number.querySelector(".digit.active")
                            .offsetWidth,
                        ease: "expo.out",
                        delay: index * 0.5,
                    });
                });
            }, 1000);
        });
    });
}

