jQuery.fn.scrollCenter = function (elem, speed) {
    var active = jQuery(this).find(elem); // find the active element
    var activeWidth = active.width(); // get active width center

    var pos = active.position().left + activeWidth; //get left position of active li + center position
    var currentscroll = jQuery(this).scrollLeft(); // get current scroll position
    var divwidth = jQuery(this).width(); //get div width
    pos = pos + currentscroll - divwidth / 2; // for center position if you want adjust then change this
    jQuery(this).animate(
        {
            scrollLeft: pos,
        },
        speed == undefined ? 1000 : speed
    );
    return this;
};

jQuery.fn.scrollleft = function (elem, speed) {
    jQuery(this).animate(
        {
            scrollLeft:
                jQuery(this).scrollLeft() -
                jQuery(this).offset().left +
                jQuery(elem).offset().left,
        },
        speed == undefined ? 1000 : speed
    );
    return this;
};

if ($("#menu-center").length) {
    $("document").ready(function ($) {
        var nav = $("#menu-center");

        $(document).on("scroll", onScroll);
    });

    function onScroll(event) {
        var scrollPos = $(document).scrollTop();
        $("#menu-center a.scrollable").each(function () {
            var currLink = $(this);
            var refElement = $(currLink.data("target"));
            if (
                refElement.position().top <= scrollPos &&
                refElement.position().top + refElement.height() > scrollPos
            ) {
                $("#menu-center ul li a.scrollable").removeClass("active");
                currLink.addClass("active");
            } else {
                currLink.removeClass("active");
            }
        });
    }
}

