if ($(".accordion").length) {
    // lk accordion-item'e active class ekle ve ac-content'i ak olarak getir
    $(".accordion .accordion-item:first")
        .addClass("active")
        .find(".ac-content")
        .slideDown();

    $(".accordion .accordion-item").click(function (j) {
        var dropDown = $(this).closest(".accordion-item").find(".ac-content");
        $(this)
            .closest(".accordion")
            .find(".ac-content")
            .not(dropDown)
            .slideUp();
        if ($(this).hasClass("active")) {
            $(this).removeClass("active");
        } else {
            $(this)
                .closest(".accordion")
                .find(".accordion-item.active")
                .removeClass("active");
            $(this).addClass("active");
        }
        // .title-content active class toggle
        if ($(this).find(".title-content").hasClass("active")) {
            $(this).find(".title-content").removeClass("active");
        } else {
            $(this)
                .closest(".accordion")
                .find(".accordion-item .title-content.active")
                .removeClass("active");
            $(this).find(".title-content").addClass("active");
        }

        dropDown.stop(false, true).slideToggle();
        j.preventDefault();
    });
}

